import React, {Component} from 'react';
// import { Editor } from 'react-draft-wysiwyg';
// import 'react-draft-wysiwyg.css';
import ImageMapper from 'react-image-mapper';
import './App.css';
import * as ReactDOM from "react-dom";
import Img from 'react-image'
import request from "superagent";

var MAP = {
    name: "my-map",
    areas: [
        {_id: "fant_youth", shape: "poly", coords: [507, 99, 917, 225, 913, 324, 491, 265]},
        {_id: "en_lit", shape: "poly", coords: [938, 351, 1187, 363, 1187, 491, 925, 484]},
        {_id: "short_nov", shape: "poly", coords: [1548, 278, 2048, 257, 2045, 361, 1545, 345]},
        {_id: "comics", shape: "poly", coords: [1208, 513, 1513, 498, 1509, 621, 1201, 632]},
        {_id: "theater", shape: "poly", coords: [2069, 532, 2473, 569, 2464, 746, 2069, 649,]},
        {_id: "poetry", shape: "poly", coords: [909, 660, 1173, 657, 1173, 801, 906, 822]},
        {_id: "essays", shape: "poly", coords: [1198, 657, 1506, 648, 1506, 792, 1186, 796]},
        {_id: "sci_fi", shape: "poly", coords: [437, 792, 862, 842, 840, 1024, 424, 994]},
        {_id: "art_book", shape: "poly", coords: [1530, 798, 2017, 845, 1997, 994, 1517, 966]},
        {_id: "detective", shape: "poly", coords: [1186, 1009, 1493, 990, 1474, 1154, 1393, 1176, 1177, 1169]},
        {
            _id: "french_lit",
            shape: "poly",
            coords: [2056, 1198, 2022, 1221, 2021, 1234, 2288, 1296, 2382, 1282, 2360, 1458, 2090, 1390, 2088, 1352, 2004, 1334, 2018, 1190]
        },
        {_id: "add", shape: "poly", coords: [945, 1252, 2088, 1354, 2092, 1506, 625, 1362, 801, 1262, 900, 1277]},
        {_id: "landscape", shape: "poly", coords: [5, 6, 117, 1, 130, 641, 157, 865, 100, 1185, 86, 1548, 4, 1558,]}
    ]
};

var categories_map = [
    ['Fantastic youth', 'fant_youth'],
    ['English literature', 'en_lit'],
    ['Short novels', 'short_nov'],
    ['Comics', 'comics'],
    ['Theater plays', 'theater'],
    ['Poetry', 'poetry'],
    ['Essays', 'essays'],
    ['Art books', 'art_book'],
    ['Science-fiction novels', 'sci_fi'],
    ['Detective novels', 'detective'],
    ['French short_nov', 'french_lit']
]

var URL = "img/2.jpg";

var mapper_original_height = 1817;
var mapper_original_width = 2559;
var mapper_ratio_h_w = mapper_original_height / mapper_original_width;
var mapper_ratio_w_h = mapper_original_width / mapper_original_height;

var book_original_height = 1632;
var book_original_width = 2493;
var book_ratio_h_w = book_original_height / book_original_width;
var book_ratio_w_h = book_original_width / book_original_height;

//
// <ImageMapper ref="mapper" src={URL} map={MAP} height={this.props.windowWidth * mapper_ratio_h_w}
//              width={this.props.windowHeight * mapper_ratio_w_h}
//              onImageClick={(e) => this.onImageClick(e)}/>
var FindPosition = function (oElement) {
    if (typeof( oElement.offsetParent ) != "undefined") {
        for (var posX = 0, posY = 0; oElement; oElement = oElement.offsetParent) {
            posX += oElement.offsetLeft;
            posY += oElement.offsetTop;
        }
        return [posX, posY];
    }
    else {
        return [oElement.x, oElement.y];
    }
}

var GetCoordinates = function (e, image_node, previous_result) {
    var PosX = 0;
    var PosY = 0;
    var ImgPos;
    ImgPos = FindPosition(image_node);
    if (!e) var e = window.event;
    if (e.pageX || e.pageY) {
        PosX = e.pageX;
        PosY = e.pageY;
    }
    else if (e.clientX || e.clientY) {
        PosX = e.clientX + document.body.scrollLeft
            + document.documentElement.scrollLeft;
        PosY = e.clientY + document.body.scrollTop
            + document.documentElement.scrollTop;
    }
    PosX = PosX - ImgPos[0];
    PosY = PosY - ImgPos[1];
    previous_result += PosX;
    previous_result += ", ";
    previous_result += PosY;
    previous_result += ", ";
    return previous_result
    // console.log(PosX, PosY)
};

var previous = [];
class App extends Component {

    constructor(props) {
        super(props);
        var mapper_new_width = window.innerHeight * mapper_ratio_w_h;
        var mapper_new_height = mapper_new_width * mapper_ratio_h_w;

        console.log(window.innerWidth, window.innerHeight, mapper_original_width, mapper_original_height, mapper_new_width, mapper_new_height)

        var new_map = JSON.parse(JSON.stringify(MAP));

        for (var i = 0; i < MAP.areas.length; i++) {
            for (var j = 0; j < MAP.areas[i].coords.length; j += 2) {
                var x = MAP.areas[i].coords[j];
                var y = MAP.areas[i].coords[j + 1];

                var new_x = mapper_new_width * x / mapper_original_width;
                var new_y = mapper_new_height * y / mapper_original_height;

                new_map.areas[i].coords[j] = new_x;
                new_map.areas[i].coords[j + 1] = new_y;

            }
        }

        request('POST', 'http://localhost:5000/api/login')
            .send({username: 'user1', password: '1234'})
            .set('Content-Type', 'application/json')
            .end(function (err, res) {
                    this.setState({
                        token: res.body.access_token
                    })
                }.bind(this)
            );

        var book_width_tmp = window.innerHeight * book_ratio_w_h;
        var book_height_tmp = book_width_tmp * book_ratio_h_w;

        this.state = {
            map: new_map,
            mapper_height: mapper_new_height,
            mapper_width: mapper_new_width,
            book_width: book_width_tmp,
            book_height: book_height_tmp,
            book_summary_y: (window.innerHeight - book_height_tmp) / 2 + 110,
            book_summary_x: (window.innerWidth - book_width_tmp) / 2 + 175,
            books_data: [],
            book_data: {}
        };
    }

    onImageClick(e) {
        var node = ReactDOM.findDOMNode(this.refs.sum_img)
        previous = GetCoordinates(e, node, previous)
        console.log(previous)
    }

    onClickMapper(area, index, e) {
        var mapper = ReactDOM.findDOMNode(this.refs.mapper);
        mapper.setAttribute("hidden", "true");

        var category_short_name = area._id;
        var category_id = 0;

        for (var item of categories_map) {
            if (item[1] === category_short_name) {
                break
            }
            category_id += 1
        }

        request('GET', 'http://localhost:5000/api/books/' + categories_map[category_id][1])
            .send({username: 'user1', password: '1234'})
            .set('Content-Type', 'application/json')
            .set('Authorization', 'JWT ' + this.state.token)
            .end(function (err, res) {
                    this.setState({
                        "books_data": res.body
                    });
                }.bind(this)
            );

        var summary = ReactDOM.findDOMNode(this.refs.summary);
        summary.removeAttribute("hidden");

        var home = ReactDOM.findDOMNode(this.refs.home);
        home.removeAttribute("hidden");
    }

    reload() {
        window.location.reload(true);
    }

    onBookListClick(e, id){
        console.log("Clicked on item " + id)

        request('GET', 'http://localhost:5000/api/books/' + id)
            .send({username: 'user1', password: '1234'})
            .set('Content-Type', 'application/json')
            .set('Authorization', 'JWT ' + this.state.token)
            .end(function (err, res) {
                    this.setState({
                        "book_data": res.body
                    });
                }.bind(this)
            );

        var summary_content = document.getElementById("summary_content");
        summary_content.setAttribute("hidden", "true")

        var summary_content = document.getElementById("book_content");
        summary_content.removeAttribute("hidden")
    }

    render() {
        console.log(this.state)
        return (
            <div id="main">
                <div ref="mapper" id="mapper">
                    <center>
                        <ImageMapper src={URL} map={this.state.map}
                                     width={this.state.mapper_width}
                            // onImageClick={(e) => this.onImageClick(e)}
                                     onClick={(area, index, e) => this.onClickMapper(area, index, e)}
                        />
                    </center>
                </div>

                <div ref="summary" id="summary" hidden="true"
                     style={{'background-size': this.state.book_width + 'px ' + this.state.book_height + 'px'}}>

                    <Img src="img/porte.jpg" onClick={() => this.reload()} width="100" ref="home" hidden="true"/>

                    <div ref="summary_content" id="summary_content"
                         style={{"top": this.state.book_summary_y + "px", "left": this.state.book_summary_x + "px"}}>
                        <CustomList books_data={this.state.books_data} onClick={this.onBookListClick.bind(this)}/>
                    </div>
                    <div ref="book_content" id="book_content" hidden="true"
                         style={{"top": this.state.book_summary_y + "px", "left": this.state.book_summary_x + "px"}}>
                        {this.state.book_data.text}
                    </div>
                </div>


            </div>

        );
    };
}

class CustomList extends Component {
    render() {

        var namesList = this.props.books_data.map(function (data, index) {
            // var namesList = list_data.map(function(data, index){
            return <li key={ index } onClick={(e) => this.props.onClick(e, data.id)}>{data.book_name} by {data.pseudo}
                ({data.author})</li>;
        }.bind(this))

        console.log(this)
        return <ul>{ namesList }</ul>
    }

}


export default App;



